section.tours {
    position: relative;
    background-color: #eff3f8;

    h1 {
        width: 50%;
        margin: 40px 0;
        position: absolute;
        top: 0;
        left: 0;
        font-size: 48px;
        color: #43b1c9;
        text-align: center;
        font-weight: 400;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    }

    div {
        width: 1200px;
        margin: 20px auto 30px auto;
        padding-top: 160px;
        display: flex;
        flex-basis: 350px;
        justify-content: space-around;
        flex-wrap: wrap;

        div {
            display: flex;
            padding-top: 0;
            padding-bottom: 20px;
            flex-direction: column;
            justify-content: space-between;
            -webkit-box-shadow: 0px 0px 14px -2px rgba(0, 0, 0, 0.75);
            -moz-box-shadow: 0px 0px 14px -2px rgba(0, 0, 0, 0.75);
            box-shadow: 0px 0px 14px -2px rgba(0, 0, 0, 0.75);
            overflow: hidden;
            background-color: #fff;

            .tour-photo {
                width: 350px;
                max-height: 200px;
                padding: 0;
                margin: 0;
                position: relative;

                -webkit-box-shadow: 0px 0px 0px 0px;
                -moz-box-shadow: 0px 0px 0px -0px;
                box-shadow: 0px 0px 0px 0px;

                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%) scale(.9);
                    transition: .3s;
                }
            }

            h2 {
                margin: 20px 0 10px 0;
                font-size: 22px;
                text-transform: uppercase;
                text-align: center;
                font-weight: 400;
                font-style: italic;
                color: #43b1c9;
                letter-spacing: 2px;
                background-color: #fff;
                z-index: 3;
            }

            h3 {
                font-size: 16px;
                letter-spacing: 1px;
                padding: 10px 20px;
            }

            p {
                height: 110px;
                padding: 0 20px;
                font-size: 12px;
                color: rgba(0, 0, 0, 0.6);
                font-style: italic;
                text-align: justify;
                letter-spacing: 1px;
                line-height: 180%;

                span {
                    display: block;
                    margin-top: 10px;
                    margin-left: 50%;
                    font-style: normal;
                }

            }

            button {
                width: 150px;
                margin: 20px auto 0 auto;
                padding: 10px 0;
                border: 1px solid black;
                background-color: #fff;
                transition: .3s;

                a {
                    padding: 20px;
                    text-transform: uppercase;
                    text-decoration: none;
                    color: #000;
                }
            }

            button:hover {
                background-color: #43b1c9;
                border-color: #43b1c9;

                a {
                    color: #fff;
                }
            }
        }

        div:hover {
            .tour-photo {
                img {
                    transform: translate(-50%, -50%) scale(1.1);
                }
            }
        }
    }
}

section.tours.blur {
    filter: blur(2px);
}

@media(max-width: 1200px) {
    section.tours {
        h1 {
            top: 20px;
            margin: 0;
            padding: 40px 0 0 0;
            font-size: 40px;
        }

        div {
            width: 900px;
        }
    }
}

@media(max-width: 900px) {
    section.tours {
        div {
            width: 100%;
        }
    }
}

@media(max-width: 768px) {
    section.tours {

        h1 {
            margin-bottom: 5px;
            padding: 0;
            font-size: 36px;
        }

        div {
            padding: 90px 0 0 0;
        }
    }
}

@media(max-width: 480px) {
    section.tours {

        h1 {
            font-size: 30px;
        }

        div {
            padding: 80px 0 0 0;
        }
    }
}

@media(max-width: 360px) {
    section.tours {
        padding-bottom: 10px;

        div {
            display: block;

            div {
                width: 90%;

                .tour-photo {
                    width: 100%;
                }

                h2 {
                    font-size: 18px;
                }

                h3 {
                    font-size: 14px;
                }

                p {
                    font-size: 10px;
                }
            }
        }
    }
}